import React, { Component } from 'react'
import "./showPage.css";


class ShowPage extends Component {

    movie = this.props.location.movie;

    render() {
        console.log(`this movie click : ${this.movie.title}`);
        return (
            
            <div className="movieInfo" style={{backgroundImage:'url(https://images.alphacoders.com/633/633643.jpg)'}}>
            <div className="movieContent">
              <img className="poster" src={`https://image.tmdb.org/t/p/w300${this.movie.poster_path}`}/>
              <h4>{this.movie.title ? this.movie.title : this.movie.name}</h4>
              <p className="movieText">{this.movie.overview}</p>
              <p className="movieVote">{this.movie.vote_average}</p>
            </div>
          </div>
            
        
        )
    } 


}
export default ShowPage;