import React ,{ Component } from "react";
import { BrowserRouter, Link, Switch, Route } from 'react-router-dom';
import Login from '../Login/Login'
import Registr from '../registr/Registr'


class NewPage extends Component{
 render(){
     return(
             <div className="movieInfo" style={{backgroundImage:'url(https://images.alphacoders.com/633/633643.jpg)'}}>
             <h1>Welcome To Foxx Movie</h1>
             <BrowserRouter>

             <Link to="/Login">Click Her To Login</Link>
             <Link to="/Registr">Click Her To Registr</Link>

            <Switch>
                <Route exact path='/Login' render={() => <Login/>} />
                <Route exact path='/Registr' render={() => <Registr/>}/>
            </Switch>

             </BrowserRouter>
         </div>
     )
 }





}


export default NewPage