import React, { Component } from 'react';
import axios from "../../../../server/node_modules/axios";
import { Redirect } from 'react-router-dom';
import './Login.css';


class Login extends Component {

    state = { email: "", password: "", RedirectToHome: false, isError: false }
// ____

    login = () => {
        this.setState({ isError: false });

        axios.post('/users/login', {
            email: this.state.email,
            password: this.state.password
        }).then(res => {
            if (res.status === 200) {
                // res.data is user
                this.setState({ RedirectToHome: true });

                sessionStorage.setItem("user",  JSON.stringify(res.data));
                this.props.setUser(res.data);
            }
            else {
                this.setState({ isError: true })
                console.log(`error code : ${res.status}`);

            }

        }).catch(err => {
            this.setState({ isError: true })
            console.log(err);
        })
    }
// ____

    render() {
        const disabled = !this.state.email || !this.state.password;
        
        if (this.state.RedirectToHome) {
            return <Redirect to='/' />
        }

        return (
            <div className='Login-User'>
                <h1>Login Page</h1>
                <input type='email' onChange={evt => this.setState({ email: evt.target.value })}
                    required='required' placeholder='email' />
                <br />
                <input type="password" onChange={evt => this.setState({ password: evt.target.value })}
                    required='required' placeholder='password' />
                <br />
                {this.state.isError ? <p style={{ color: 'red' }}>Login Error</p> : ""}
                <button disabled={disabled} onClick={this.login}>Login</button>

            </div>
        )
    }
}

export default Login;