import React, { Component } from 'react';
import { BrowserRouter, Link, Switch, Route } from 'react-router-dom';
import './App.css';
import Login from './components/Login/Login';
import Home from './components/home/Home';
import MovieList from './components/Movie List/movie_list_Results';
import Registr from './components/Registr/Registr';
import Logout from './components/Logout/Logout';
import ShowPage from "./components/showPage/showPage";
import Cart from "./components/Cart/Cart";
import NewPage from "./components/newUsersPage/newUserPage";


class App extends Component {

  state = { user: null };// now is empty

  setUser = user => {
    this.setState({ user: user }); 
  }

  render() {
  
    return (
      <BrowserRouter>
        <div className="App">
          <div className="LinkHead">
            <img id="myLogo" src="https://i.pinimg.com/originals/d0/7e/81/d07e81371cfc20ad1bf670847cc3fa57.jpg"/>
            <Link to='/'>Home</Link>
            {!this.state.user ? <Link to='/Login'>Login</Link> : ""}
            {!this.state.user ? <Link to='/Registr'>Registr</Link> : ""}
            {this.state.user ? <Link to='/Cart'>MyCart</Link> : ""}
            {this.state.user ? <Link to='/Logout'>Logout</Link> : ""}
            {this.state.user ? <Link to='/NewPage'>new Page</Link> : ""}


          </div>

          <Switch>
            < Route exact path='/' component={Home} />
            < Route exact path='/Login' render={() => <Login setUser={this.setUser} />} />
            <Route exact path='/Registr' render={() => <Registr setUser={this.setUser} />} />
            <Route exact path='/MovieList' component={MovieList} />
            <Route exact path='/showPage' component={ShowPage} />
            <Route exact path='/Logout' render={() => <Logout setUser={this.setUser} />} />
            <Route exact path='/Cart' render={() => <Cart setUser={this.setUser} />} />
            <Route exact path='/NewPage' render={() => <NewPage />} />
          </Switch>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
