import React from 'react';
import { Card, Col} from 'react-bootstrap';
import './Movie.css';
const Movie = (props) => {
    return (

        
            
        <Col md="3">
                    <Card >
                        {props.myImage = <Card.Img variant="top" src={`https://image.tmdb.org/t/p/w500${props.myImage}`} />}
                        <Card.Body>
                            {props.myTitle = <Card.Title className="card-title">{props.myTitle}</Card.Title>}
                            {props.myOverview = <Card.Text>{props.myOverview}</Card.Text>}
                            {props.userVote = <Card.Text>{props.userVote}</Card.Text>}
                        </Card.Body>
                    </Card>
        </Col>
        
       
                  
            
        


    )
}

export default Movie;